export default {
	data() {
		return {
			
		}
	},
	methods: {
		showItem(type){
			switch(type){
				case 'log':
				uni.navigateTo({
					url:'../../tab-item-content/work-center/work-log/work-log'
				});
				break;
				case 'express':
				uni.navigateTo({
					url:'../../tab-item-content/work-center/express-list/express-list'
				});
				break;
				case 'repair':
				uni.navigateTo({
					url:'../../tab-item-content/shop-center/shop-center'
				})
			}
		},
		showMineClick(){
			uni.navigateTo({
				url:'../mine/mine'
			})
		},
		showMessageClick(){
			uni.navigateTo({
				url:'../message/message'
			})
		},
		showIndexClick(){
			uni.navigateTo({
				url:'../index/index'
			})
		},
	}
}
